import { getActClass } from './utils';


export default {
  name: 'PressHorDialog',
  components: {
  },
  props: {
    popupWith: {
      type: String,
      default: '5.6',
    },
    title: {
      type: String,
      default: '',
    },
    isShowClose: {
      type: Boolean,
      default: true,
    },
  },
  options: {
    virtualHost: true,
  },
  emits: [
    'close',
  ],
  data() {
    return {
    };
  },
  methods: {
    close() {
      this.$emit('close');
    },
    getActClass(...args) {
      return getActClass(false, args);
    },
  },
};

